.content {
	padding: 10px;
	background: $theme-background-alt;
	border: solid 1px silver;
	border-top: none;
}

#content-top {
	display: flex;
	justify-content: space-between;
}

#server-info, #history-selector {
	margin: 5px;
}

#server-info {
	min-width: 400px
}

#header {
	width: 100%;
	background-color: $theme-color;
	height: 112px;
	position: relative;

	.site-title {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		margin-left: 30px;
		display: inline-block;
		color: $theme-color-text-special !important;
		font-size: 30px;
		.subtitle {
			text-align: center;
			font-size: 16px;
			a {
				color: inherit;
			}
		}
	}
}

#header-right {
	float: right;
	margin: 10px;
}

.row-double {
	padding: 10px;
}

.section {
	padding: 10px;
	background-color: $theme-background-alt;
	box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.1);
	.section-head {
		display: block;

		.section-title {
			font-size: 15px;
			font-weight: bold;
			text-transform: uppercase;
		}
		.section-controls {
			float: right;
			a {
				font-size: 14px;
				margin-left: 3px;
			}
		}
	}
}

a.normal {
	color: inherit;
}

#tab-bar {
	position: relative;
	margin: 10px;
	& li {
		cursor: pointer;
	}
}

.tabs {
	background-color: $theme-color-light;
	height: 47px;

	.tab-title {

		/* Floating the tab divs left removes the spacing */
		//float: left;
		border-radius: 0;
		margin: 0;
		color: $theme-color-text-special;
		display: inline-block;
		padding: 0;
		text-transform: uppercase;
		border-bottom: solid 3px transparent;
		font-weight: bold;
		cursor: pointer;
		.active, &.active, &:hover {
			border-bottom: solid 3px $theme-accent;
			color: whiteSmoke;
		}
		a {
			padding: 10px 15px;
			color: inherit;
			display: block;
		}
	}
}

a {
	color: $theme-color-links;
}

a:hover {
	color: $theme-color-text;
}

@media (max-width: 1300px) {
	.site-title {
		display: block;
		float: left;
		overflow: hidden;
	}
	.themes, #header-right {
		height: 110px;
		width: 450px;
		float: right;
		margin: 0 10px;
	}
}

/* Small screens */
@media (max-width: 1100px) {
	#header {
		height: auto;
		padding-bottom: 5px;
		.site-title {
			float: none;
			top: initial;
			transform: none;
			width: 100%;
			margin: 10px;
			text-align: center;
			position: initial;
		}
	}
	#header-right, .themes {
		width: 100%;
		margin: 5px 0;
		float: none;
		text-align: center;
	}
	#content-top {
		display: block;
	}
	#body-wrap {
		.row-double {
			padding: 0;
			#history-selector, #server-info {
				margin: 0;
				width: 100%;
				display: block;
				float: none;
			}
		}
		#top-ad {
			margin: 10px auto;
		}
		#bottom-ad {
			margin-top: 30px !important;
		}
		#tab-bar {
			margin: 0;
		}
		#tab-bar .tabs {
			height: auto;
			.tab-title {
				float: none;
			}
		}
	}
	#footer {
		height: auto;
		line-height: initial;
		padding: 20px;
		#footer-left, #footer-right {
			width: 100%;
			display: block;
			text-align: center;
		}
	}
}




