table {
	border-collapse: collapse;
	margin: ($base-spacing / 2) 0;
	table-layout: fixed;
	width: 100%;
}

th {
	border-bottom: 1px solid darken($base-border-color, 15);
	font-weight: bold;
	padding: ($base-spacing / 2) 0;
	text-align: left;
}

td {
	border-bottom: $base-border;
	padding: ($base-spacing / 2) 0;
}

tr,
td,
th {
	vertical-align: middle;
}



